import logo from './logo.svg';
import './App.css';

import AppExtensionsSDK from '@pipedrive/app-extensions-sdk';
import { Command, Modal } from '@pipedrive/app-extensions-sdk';
import { useEffect, useState } from 'react';

function App() {

  const [status, setStatus] = useState(null);
  let sdk;

  const initializeSdk = async () => {
    try {
      sdk = await new AppExtensionsSDK({ identifier: '8a8689eb-6085-4651-b1fd-3a4376ae2a84' }).initialize();
  
      const { status } = await sdk.execute(Command.OPEN_MODAL, {
        type: Modal.CUSTOM_MODAL,
        action_id: '8a8689eb-6085-4651-b1fd-3a4376ae2a84'
      });

      setStatus(status);
    } catch (error) {
      console.error('Ошибка при инициализации или выполнении команды:', error);
    }
  };
  
  useEffect(() => {
    initializeSdk().catch(console.error);
  }, []);

  useEffect(() => {
    console.log(status);
  }, [status]);

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
